<template>
    <v-card dark :color="listBackground">
        <div class="list-container">
            <single-announcment class="in-front" v-for="announcment in announcments" :key="announcment.id" :ann="announcment"/>
            <div class="in-back" v-if="enoughAnnouncments"> 
                <div class="placeholders-container">
                    <single-announcment v-for="n in 12" :key="`${n}-placeholder`" :ann="{}"/>
                </div>
            </div>
        </div>
    </v-card>
</template>

<script>
    import store from "@/store";
    import { FETCH_ANNOUNCMENTS } from "../../vuex/actions";
    import SingleAnnouncment from "./components/SingleAnnouncment.vue";
    import { mapState } from "vuex";

    export default {
      components: {
        SingleAnnouncment
      },
      created: function() {
        store.dispatch(FETCH_ANNOUNCMENTS);
      },
      computed: {
        enoughAnnouncments() {
          const count = this.announcments.length;
          return count === 0 || count >= 9;
        },
        listBackground() {
          return this.$route.name === "flats" ? "#3c3c3c" : "#1e1e1e";
        },
        ...mapState({
          announcments: state => state.announcments.announcments
        })
      },
    };
</script>

<style lang="sass" scoped>
    .v-card
        height: 100%
        border-radius: 0
    .layout
        overflow-y: scroll
        height: 100%
    .list-container
        overflow-y: scroll
        height: 100%
        display: flex
        flex-direction: row
        flex-wrap: wrap
        justify-content: space-around
        position: relative
    .list-container::-webkit-scrollbar-track
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3)
        border-radius: 10px
        background-color: #1c1c1c
    .list-container::-webkit-scrollbar
        width: 12px
        background-color: #1c1c1c
    .list-container::-webkit-scrollbar-thumb
        border-radius: 10px
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3)
        background-color: #fb7600
        border: 2px solid #d36707

    .placeholders-container
        @extend .list-container
        overflow: hidden
    .in-front
        z-index: 2
    .in-back
        position: absolute
        z-index: 1
</style>
