<template>
<div @click="click">
    <div class="triangle"></div>
    <v-icon 
      @mouseover="mouseOver" 
      @mouseout="mouseOut" 
      :class="{'dim': hovered && !favorite}"
      color="#f44336" >{{active ? "favorite" : "favorite_border"}}</v-icon>
</div>
    
</template>

<script>
  import store from "@/store";
  import { POST_FAVORITE } from "../../../vuex/actions";
  export default {
    data() {
      return {
        hovered: false
      };
    },
    props: {
      favorite: Boolean,
      id: String
    },
    computed: {
      active() {
        return this.favorite || this.hovered;
      }
    },
    methods: {
      mouseOver() {
        this.hovered = !this.hovered;
      },
      mouseOut() {
        this.hovered = !this.hovered;
      },
      click() {
        store.dispatch(POST_FAVORITE, { id: this.id });
      }
    }
  };
</script>

<style lang="sass" scoped>
    $triangle-size: 40px
    .triangle
      cursor: pointer
      height: 0px
      width: 0px
      border-top: $triangle-size solid #fff
      border-left: $triangle-size solid #fff
      border-right: $triangle-size solid transparent
      border-bottom: $triangle-size solid transparent
      position: relative
    .v-icon
      font-size: 40px
      font-weight: bold
      position: absolute 
      cursor: pointer
      margin: 5px
      top: 0
      left: 0
      user-select: none
    .dim
      opacity: 0.3
</style>
